import React, { Component } from "react";
//import SaveBtn from "../../components/SaveBtn";
import Jumbotron from "../../components/Jumbotron";
import API from "../../utils/API";
import { Link } from "react-router-dom";
import { Col, Row, Container } from "../../components/Grid";
import { List, ListItem } from "../../components/List";
import { Input, FormBtn } from "../../components/Form";
//import Card from "../../components/Card"

class Articles extends Component {
  state = {
    article: [],
    topic: "",
    startDate: "",
    endDate: "",
    url: ""
  };

  componentDidMount() {
    this.loadArticles();
  }

  loadArticles = () => {
    API.getArticles()
      .then(res =>
        this.setState({ article: res.data, title: "", date: "", url: "" })
      )
      .catch(err => console.log(err));
  };

  deleteArticle = id => {
    API.deleteArticle(id)
      .then(res => this.loadArticles())
      .catch(err => console.log(err));
  };

  saveArticle = id => {
    API.saveArticle(id)
      .then(res => this.loadArticles())
      .catch(err => console.log(err));
  };

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  // handleFormSubmit = event => {
  //   event.preventDefault();
  //   if (this.state.title && this.state.author) {
  //     API.saveBook({
  //       title: this.state.title,
  //       author: this.state.author,
  //       synopsis: this.state.synopsis
  //     })
  //       .then(res => this.loadBooks())
  //       .catch(err => console.log(err));
  //   }
  // };

  render() {
    return (
      <Container fluid style={{ justifyContent: 'center', alignItems: 'center' }}>
        <Jumbotron />
        <Row>
        <Col size="md-6">
        <form>
          <Input
            value={this.state.topic}
            onChange={this.handleInputChange}
            name="topic"
            placeholder="What would you like to search for today?" />
          <Input
            value={this.state.startDate}
            onChange={this.handleInputChange}
            name="startDate"
            placeholder="Start Date"
            type="date"
            max="3000-12-31"
            min="1000-01-01" />
          <Input
            value={this.state.endDate}
            onChange={this.handleInputChange}
            name="endDate"
            placeholder="End Date"
            type="date"
            max="30001231"
            min="10000101" />
            <FormBtn
                disabled={!(this.state.topic && this.state.startDate && this.state.endDate)}
                onClick={this.handleFormSubmit}
              >
                Submit 
              </FormBtn>
        </form>
        </Col>
        <Col size="md-6">
        {this.state.article.length ? (
              <List>
                {this.state.article.map(data => (
                  <ListItem key={data._id}>
                    <Link to={"/articles/" + data._id}>
                      <strong>
                        {data.title} by {data.author}
                      </strong>
                    </Link>
                  </ListItem>
                ))}
              </List>
            ) : (
              <h3>No Results to Display</h3>
            )}


      </Col>
      </Row>
      </Container>
    );
  }
}

export default Articles;


